/* eslint-disable no-undef */
import PropTypes from 'prop-types'
import {
  AlipayOutlined,
  LockOutlined,
  MobileOutlined,
  TaobaoOutlined,
  UserOutlined,
  WeiboOutlined
} from '@ant-design/icons'
import {
  LoginFormPage,
  ProFormCaptcha,
  ProFormText
} from '@ant-design/pro-components'
import { Divider, message, Space, Tabs, Form, ConfigProvider } from 'antd'
import React, { useEffect, useState } from 'react'
import mylogo1 from '../../img/graduation-7287004_1920.jpg'
import mylogo2 from '../../img/sun.png'
import './login.css'
import { withRouter } from 'react-router-dom'
import axios from 'axios'
const headers = {
  'Content-Type': 'application/x-www-form-urlencoded'
}
const LoginType = 'phone' | 'account'
const iconStyles = {
  color: 'rgba(0, 0, 0, 0.2)',
  fontSize: '18px',
  verticalAlign: 'middle',
  cursor: 'pointer'
}
function Login (props) {
  // 实现登录错误3次后等待1分钟的功能的常量
  const [errorCount, setErrorCount] = useState(0) // 表示登录操作的错误次数
  const [waitingTime, setWaitingTime] = useState(0) // 表示等待时间
  const [isLoading, setIsLoading] = useState(false) // 表示登录按钮是否处于加载状态
  const [isButtonDisabled, setIsButtonDisabled] = useState(false) // 用于跟踪登录按钮的禁用状态
  // 实现登录错误3次后等待1分钟的功能的useEffect
  useEffect(() => {
    let timeoutId
    if (
      errorCount >= 3 &&
      parseInt(localStorage.getItem('waitingTime'), 10) === 0
    ) {
      setIsButtonDisabled(true)
      if (waitingTime === 0) {
        setWaitingTime(60) // 设置等待时间为60秒
      }
      timeoutId = setInterval(() => {
        setWaitingTime((prevTime) => prevTime - 1)
      }, 1000)
    }
    return () => {
      clearTimeout(timeoutId)
    }
  }, [errorCount])
  // 1分钟等待时间
  useEffect(() => {
    if (waitingTime <= 0) {
      setIsButtonDisabled(false)
      setErrorCount(0)
    }
  }, [waitingTime])
  // 恢复错误次数和等待时间的useEffect
  useEffect(() => {
    const storedErrorCount = localStorage.getItem('errorCount') // 错误次数
    const storedWaitingTime = localStorage.getItem('waitingTime') // 等待时间
    if (storedErrorCount) {
      // 如果storedErrorCount存在就把errorCount设置
      setErrorCount(parseInt(storedErrorCount, 10))
    }
    if (storedWaitingTime) {
      // 同上
      setWaitingTime(parseInt(storedWaitingTime, 10))
    }
  }, [])
  // 更新错误次数和等待时间的useEffect（随时更新随着errorCount, waitingTime变化）
  useEffect(() => {
    localStorage.setItem('errorCount', errorCount.toString())
  }, [errorCount])
  useEffect(() => {
    localStorage.setItem('waitingTime', waitingTime.toString())
  }, [waitingTime])

  // 登录的axios fetchData
  async function fetchData (values) {
    if (errorCount >= 3) {
      // 正在等待中，不执行登录操作
      message.error('正在等待中，不执行登录操作')
      return
    }
    const data = new URLSearchParams()
    data.append('username', values.username)
    data.append('password', values.password)
    try {
      setIsLoading(true)
      await new Promise((resolve) => setTimeout(resolve, 1000)) // 模拟异步请求等待2秒
      const response = await axios.post('http://47.237.100.43:8080/login', data, {
        headers
      })
      if (response.data.data.authorities[0].authority === 'ROLE_USER') {
        // 存请求头
        localStorage.setItem(
          'authorizationToken',
          response.headers.get('authorization')
        )
        localStorage.setItem(
          'refreshToken',
          response.headers.get('refresh-token')
        )
        console.log(response.data.data.username)
        // 存用户信息(token)
        // 登录的token
        localStorage.setItem('token', JSON.stringify(response.data))
        props.history.push('/userprofile')
        message.success('登录成功')
        setIsLoading(false)
      } else {
        message.error('此账号为管理员账号')
        setIsLoading(false)
      }
    } catch (error) {
      setIsLoading(true)
      setErrorCount((prevCount) => prevCount + 1)
      message.error('用户名或密码错误')
      setIsLoading(false)
    }
  }
  const [loginType, setLoginType] = useState(LoginType)
  const users = localStorage.getItem('token')
  useEffect(() => {
    if (users) {
      window.history.pushState(null, '', '/login')
    }
  }, [])
  return (
    <div
      style={{
        backgroundColor: 'white',
        height: '100vh'
      }}
    >
      <div
        style={{
          width: '100%',
          height: '4.5vh',
          position: 'absolute',
          backgroundColor: 'rgb(252, 139, 0,0.4)'
        }}
      >
        <div className="nav-left">
          <img
            src={mylogo2}
            style={{ width: '4.5vh', height: '4.5vh', float: 'left' }}
          ></img>
          <div className="nav-left-span">
            <a
              onClick={() => {
                props.history.push('/home')
              }}
              style={{ color: 'white' }}
            >
              阳光高考
            </a>
          </div>
        </div>
        <div className="nav-right">
          <a
            onClick={() => {
              props.history.push('/home')
            }}
          >
            首页
          </a>
          <Divider
            type="vertical"
            style={{ borderWidth: '1px', borderColor: 'white' }}
          />
          <a
            onClick={() => {
              props.history.push('/helpcenter')
            }}
          >
            帮助中心
          </a>
          <Divider
            type="vertical"
            style={{ borderWidth: '1px', borderColor: 'white' }}
          />
          <a
            href="//www.chsi.com.cn/about/contact.shtml"
            target="_blank"
            rel="noreferrer"
            style={{ textDecoration: 'none' }}
          >
            联系我们
          </a>
        </div>
      </div>

      <ConfigProvider
        theme={{
          token: {
            colorPrimary: 'rgb(255, 144, 16)'
          }
        }}
      >
        <LoginFormPage
          backgroundImageUrl={mylogo1}
          logo={mylogo2}
          title="阳光高考"
          subTitle="教育部高校招生阳光工程指定平台"
          disabled={isLoading || isButtonDisabled}
          onFinish={(values) => {
            // 在这里执行登录逻辑
            console.log('登录按钮点击了') // http://localhost:4000/users?username=${values.username}&password=${values.password}
            console.log('用户输入的值：', values)
            fetchData(values)
          }}
          actions={
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                flexDirection: 'column'
              }}
            >
              <Divider plain>
                <span
                  style={{ color: '#CCC', fontWeight: 'normal', fontSize: 14 }}
                >
                  其他登录方式
                </span>
              </Divider>

              <Space align="center" size={24}>
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    flexDirection: 'column',
                    height: 40,
                    width: 40,
                    border: '1px solid #D4D8DD',
                    borderRadius: '50%'
                  }}
                >
                  <AlipayOutlined style={{ ...iconStyles, color: '#1677FF' }} />
                </div>
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    flexDirection: 'column',
                    height: 40,
                    width: 40,
                    border: '1px solid #D4D8DD',
                    borderRadius: '50%'
                  }}
                >
                  <TaobaoOutlined style={{ ...iconStyles, color: '#FF6A10' }} />
                </div>
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    flexDirection: 'column',
                    height: 40,
                    width: 40,
                    border: '1px solid #D4D8DD',
                    borderRadius: '50%'
                  }}
                >
                  <WeiboOutlined style={{ ...iconStyles, color: '#333333' }} />
                </div>
              </Space>
            </div>
          }
        >
          <Tabs
            centered
            activeKey={loginType}
            onChange={(activeKey) => setLoginType(activeKey)}
          >
            <Tabs.TabPane key={'account'} tab={'账号密码登录'} />
            <Tabs.TabPane key={'phone'} tab={'手机号登录'} />
          </Tabs>
          {loginType === 'account' && (
            <>
              <ProFormText
                name="username"
                fieldProps={{
                  size: 'large',
                  prefix: <UserOutlined className={'prefixIcon'} />
                }}
                placeholder={'请输入用户名'}
                rules={[
                  {
                    required: true,
                    message: '请输入用户名!'
                  }
                ]}
              />
              <ProFormText.Password
                name="password"
                fieldProps={{
                  size: 'large',
                  prefix: <LockOutlined className={'prefixIcon'} />
                }}
                placeholder={'请输入密码！'}
                rules={[
                  {
                    required: true,
                    message: '请输入密码！'
                  }
                ]}
              />
            </>
          )}
          {loginType === 'phone' && (
            <>
              <ProFormText
                fieldProps={{
                  size: 'large',
                  prefix: <MobileOutlined className={'prefixIcon'} />
                }}
                name="mobile"
                placeholder={'手机号'}
                rules={[
                  {
                    required: true,
                    message: '请输入手机号！'
                  },
                  {
                    pattern: /^1\d{10}$/,
                    message: '手机号格式错误！'
                  }
                ]}
              />
              <ProFormCaptcha
                fieldProps={{
                  size: 'large',
                  prefix: <LockOutlined className={'prefixIcon'} />
                }}
                captchaProps={{
                  size: 'large'
                }}
                placeholder={'请输入验证码'}
                captchaTextRender={(timing, count) => {
                  if (timing) {
                    return `${count} ${'获取验证码'}`
                  }
                  return '获取验证码'
                }}
                name="captcha"
                rules={[
                  {
                    required: true,
                    message: '请输入验证码！'
                  }
                ]}
                onGetCaptcha={async () => {
                  message.success('获取验证码成功！验证码为：1234')
                }}
              />
            </>
          )}
          <div
            style={{
              marginBlockEnd: 24
            }}
          >
            <a
              onClick={() => {
                props.history.push('/register/registerselect')
              }}
              className='myorange'
            >
              注册
            </a>
            <a
              style={{
                float: 'right'
              }}
              className='myorange'
              onClick={() => {
                props.history.push('/forgotpassword')
                setErrorCount(0)
                setWaitingTime(0)
                setIsButtonDisabled(false)
                console.log(waitingTime)
                console.log(errorCount)
              }}
            >
              忘记密码
            </a>
          </div>
          <div>
            {errorCount >= 3 && waitingTime > 0 && (
              <Form.Item>
                登录错误次数过多，请等待 {waitingTime} 秒后再试。
              </Form.Item>
            )}
          </div>
        </LoginFormPage>
      </ConfigProvider>
    </div>
  )
}
export default withRouter(Login)
Login.propTypes = {
  history: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
  match: PropTypes.object.isRequired
}
